@import 'styles/_constants';

.base {
    width: 100%;
}

.source {
    display: flex;
    justify-content: center;
    overflow: auto;
}

.sequence {
    white-space: nowrap;
    height: 75px;
    padding: 2px;
    overflow: auto;
    margin: 2px;
    border-radius: 5px;
    
    &__over {
        background-color: lighten($base-color, 20);
    }
}
