@import 'styles/_constants';
@import 'styles/_mixins';

.base {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
    width: 100%;
    border: 2px solid $secondary-color;
    border-radius: 5px;
}

.caption {
    text-align: center;
    color: $secondary-text-color;
}

.notes {
    width: 50%;
}

.bpm {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $secondary-text-color;
}

.sequence {
    width: 100%;

    @include small-desktop {
        width: 90%;
    }
    
    @include big-desktop {
        width: 80%;
    }
}

.caption {
    margin-top: 10px;
}

.action {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 10px 0;
    justify-content: center;
    align-items: center;
}

.info {
    color: $secondary-text-color;
    padding: 10px;
}

.score {
    color: $base-text-color;
    padding: 10px;
}

.start {
    width: 200px;
    height: 40px;
    border: 1px solid $secondary-color;
    border-radius: 5px;
    background-color: darken($secondary-color, 10);
    color: lighten($base-text-color, 10);
    font-size: 20px;
    font-weight: bold;
}

@keyframes blink {
    0% {
        opacity: 0;
    }

    10% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}

.blink {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    border: 2px solid $base-color;
    opacity: 0;
    animation: blink 0.2s;
    pointer-events: none;
    z-index: 10;
}

.ticks {
    width: 100%;
    height: 50px;
    border: 1px solid $secondary-color;
    border-radius: 5px;
}
