@import 'styles/_constants';

.base {
    display: flex;
    flex-direction: row;
    flex-grow: 2;
    height: 50px;
}

.value {
    width: 100px;
    height: 46px;
    padding-left: 15px;
    border: 1px solid darken($secondary-color, 10);
    font-size: 30px;
    text-align: center;
    color: $base-text-color;
}

.button {
    width: 40px;
    height: 50px;
    line-height: 50px;
    background-color: darken($secondary-color, 20);
    color: lighten($base-text-color, 20);
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    user-select: none;

    &:nth-child(1),
    &:nth-last-child(1) {
        background-color: darken($secondary-color, 30);
    }
    &:nth-child(2),
    &:nth-last-child(2) {
        background-color: darken($secondary-color, 20);
    }
    &:nth-child(3),
    &:nth-last-child(3) {
        background-color: darken($secondary-color, 10);
    }

    &:nth-child(1) {
        border-radius: 5px 0 0 5px;
    }

    &:nth-last-child(1) {
        border-radius:  0 5px 5px 0;
    }

    &:hover {
        background-color: darken($secondary-color, 35);
    }

    &:active {
        background-color: darken($secondary-color, 50);
    }
}
