@mixin phone {
    @media (max-width: 480px) { @content; }
}
@mixin tablet {
    @media (min-width: 481px) and (max-width: 1024px) { @content; }
}
@mixin small-desktop {
    @media (min-width: 1025px) and (max-width: 1366px) { @content; }
}
@mixin big-desktop {
    @media (min-width: 1367px) { @content; }
}
